
.post-comments {
  padding-bottom: 9px;
  margin: 5px 0 5px;
}
.comments-nav {
  border-bottom: 1px solid #eee;
  margin-bottom: 5px;
}
.post-comments .comment-meta {
  border-bottom: 1px solid #eee;
  margin-bottom: 5px;
}
.post-comments .media {
  border-left: 1px dotted #000;
  border-bottom: 1px dotted #000;
  margin-bottom: 5px;
  padding-left: 10px;
}
.post-comments .media-heading {
  font-size: 12px;
  color: grey;
}
.post-comments .comment-meta a {
  font-size: 12px;
  color: grey;
  font-weight: bolder;
  margin-right: 5px;
}
.modalDialog {
  position: fixed;
  font-family: Arial, Helvetica, sans-serif;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99999;
  opacity: 0;
  transition: opacity 400ms ease-in;
  pointer-events: none;
}
.modalDialog:target {
  opacity: 1;
  pointer-events: auto;
}
.modalDialog>div {
  width: 400px;
  position: relative;
  margin: 10% auto;
  padding: 5px 20px 13px 20px;
  border-radius: 10px;
  background: #fff;
  background: -o-linear-gradient(#fff, #999);
}
.close {
  background: #606061;
  color: #FFFFFF;
  line-height: 25px;
  position: absolute;
  right: -12px;
  text-align: center;
  top: -10px;
  width: 24px;
  text-decoration: none;
  font-weight: bold;
  border-radius: 12px;
  box-shadow: 1px 1px 3px #000;
}
.close:hover {
  background: #00d9ff;
}
.form-signin {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}
.form-signin .checkbox {
  font-weight: normal;
}
.form-signin .form-control {
  position: relative;
  height: auto;
  box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: -1px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.login-btn {
  margin-top: 10px;
}
.or-social {
  text-align: center;
  margin: 10px 0 10px 0;
}
.facebook {
  color: white;
  outline: none;
  background-color: #4863ae;
  border-color: #4863ae;
}
.facebook:hover {
  background-color: #2871aa;
  border-color: #2871aa;
}
.twitter {
  color: white;
  outline: none;
  background-color: #46c0fb;
  border-color: #46c0fb;
}
.twitter:hover {
  background-color: #00c7fb;
  border-color: #00c7fb;
}
.google {
  color: white;
  outline: none;
  background-color: #DD4B39;
  border-color: #DD4B39;
}
.google:hover {
  background-color: #e15f4f;
  border-color: #e15f4f;
}
.github {
  color: white;
  outline: none;
  background-color: #4183C4;
  border-color: #4183C4;
}
.github:hover {
  background-color: #5490ca;
  border-color: #5490ca;
}
.margin-bottom-10 {
  margin-bottom: 10px;
}
[type=checkbox]:checked,
[type=checkbox]:not(:checked) {
  position: absolute;
  left: -9999px;
  visibility: hidden;
}
[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}
[type=checkbox]+label {
  position: relative;
  height: 25px;
}
[type=checkbox]+label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  z-index: 0;
  border: 2px solid #5a5a5a;
  border-radius: 1px;
  margin-top: 2px;
  transition: .2s;
}
[type=radio]:checked+label,
[type=radio]:not(:checked)+label,
[type=checkbox]+label {
  -khtml-user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
  font-size: 1rem;
  padding-left: 35px;
  display: inline-block;
  line-height: 25px;
}
[type=checkbox]:checked+label:before {
  top: -4px;
  left: -3px;
  width: 12px;
  height: 22px;
  border-top: 2px solid transparent;
  border-left: 2px solid transparent;
  border-right: 2px solid #4285F4;
  border-bottom: 2px solid #4285F4;
  -webkit-transform: rotate(40deg);
  transform: rotate(40deg);
  -webkit-backface-visibility: hidden;
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}
[type=checkbox]+label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  z-index: 0;
  border: 2px solid #5a5a5a;
  border-radius: 1px;
  margin-top: 2px;
  transition: .2s;
}
.error-text {
  color: #F44336;
  transition: .2s opacity ease-out, .2s color ease-out;
}
.btn-primary {
  color: #fff;
  background-color: none;
  border-color: blue;
  color: blue !important;
}
.label-info {
  background-color: none;
  color: #000;
}



